
import React, { createContext, useContext, useState } from 'react';
import { getAuth } from 'firebase/auth';

const CampaignContext = createContext();

export const useCampaign = () => useContext(CampaignContext);

export const CampaignProvider = ({ children }) => {
  const [campaigns, setCampaigns] = useState([]);
  const [campaignData, setCampaignData] = useState({
    summary: null,
    classification: null,
    plan: null,
    messagingGuide: null,
    name: '',
  });

  const fetchCampaigns = async () => {
    const auth = getAuth();
    const user = auth.currentUser;

    // DEV fallback
    if (!user) {
      console.warn(' No user logged in, loading mock campaigns.');
      const dummy = [
        {
          id: 'mock001',
          name: 'Mock Campaign',
          summary: { purpose: 'Test', audience: 'Developers', goals: ['issue awareness'] },
          classification: { primary_type: 'Union', sub_type: 'Workplace' },
        }
      ];
      setCampaigns(dummy);
      return dummy;
    }

    try {
      const token = await user.getIdToken();
      const res = await fetch('/api/campaigns', {
        headers: { Authorization: `Bearer ${token}` }
      });

      if (!res.ok) {
        throw new Error(`Server responded with ${res.status}`);
      }

      const data = await res.json();
      setCampaigns(data);
      console.log(' Campaigns loaded:', data);
      return data;
    } catch (error) {
      console.error(' Error loading campaigns:', error);
      throw error;
    }
  };

  const updateCampaignData = (newData) => {
    setCampaignData(prev => {
      const updated = { ...prev, ...newData };
      console.log(' Campaign Context Updated:', updated);
      return updated;
    });
  };

  const resetCampaignData = () => {
    const reset = {
      summary: null,
      classification: null,
      plan: null,
      messagingGuide: null,
      name: '',
    };
    setCampaignData(reset);
    console.log(' Campaign Context Reset');
  };

  const value = {
    campaigns,
    fetchCampaigns,
    campaignData,
    updateCampaignData,
    resetCampaignData,
  };

  return (
    <CampaignContext.Provider value={value}>
      {children}
    </CampaignContext.Provider>
  );
};
